import { createSlice } from "@reduxjs/toolkit";

const initialStateSession = {
	value: 25,
};

export const sessionSlice = createSlice({
	name: "session",
	initialState: initialStateSession,
	reducers: {
		increment: (state) => {
			state.value += 1;
		},
		decrement: (state) => {
			state.value -= 1;
		},
		toggleResetSession: (state) => {
			state.value = 25;
		},
	},
});

export const { increment, decrement, toggleResetSession } =
	sessionSlice.actions;

export const sessionLength = (state) => state.session.value;

export default sessionSlice.reducer;
