import { createSlice } from "@reduxjs/toolkit";

const initialStateBreak = {
	value: 5,
};

export const breakSlice = createSlice({
	name: "break",
	initialState: initialStateBreak,
	reducers: {
		increment: (state) => {
			state.value += 1;
		},
		decrement: (state) => {
			state.value -= 1;
		},
		toggleResetBreak: (state) => {
			state.value = 5;
		},
	},
});

export const { increment, decrement, toggleResetBreak } = breakSlice.actions;

export const breakLength = (state) => state.break.value;

export default breakSlice.reducer;
